$green: #00f7cf;
$purple: #6200fe;
$lightGray: #202020;
$white: #fff;

.header {
  position: fixed;
  top: 0;
  width: 100%;
  background: rgb(8, 8, 8);
  z-index: 99999;
  padding: 0.5rem 1rem;
  transition: background-color 0.2s ease-in-out;

  .site-logo {
    width: 300px;
  }

  .social-imgs {
    @media (max-width: 575.98px) {
      display: none;
    }

    .social-img {
      margin-right: 1rem;
    }
  }
}

.social-img {
  height: 45px;
  width: 45px;
}

.section {
  padding: 10rem 0 6rem;

  @media (max-width: 575.98px) {
    padding: 8rem 1rem 4rem;
  }

  .section-title-wrapper {
    position: relative;
  }

  .section-title-container {
    display: inline-block;
    padding: 2rem 3rem;
    position: absolute;
    top: -11.4rem;
    z-index: 1;

    // &:before {
    //   content: '';
    //   width: 100%;
    //   height: 10px;
    //   background: red;
    //   position: absolute;
    //   z-index: -1;
    //   bottom: 4rem;
    //   left: 0;
    // }
  }

  .section-title {
    font-family: "Bitter", serif;
    font-size: 5rem;
    margin: 0;

    @media (max-width: 575.98px) {
      font-size: 3rem;
    }
  }
}

.section-profile {
  background: #101010;
  border-bottom: 10px solid $green;
  @media (max-width: 575.98px) {
    padding-top: 5rem;
  }

  .profile-img {
    height: 20rem;
    box-shadow: 6.5px 6.5px #6101fe;

    @media (max-width: 575.98px) {
      display: none;
    }

    &:hover {
      position: relative;
      right: 5px;
      bottom: 5px;
      box-shadow: 10px 10px #6101fe;
    }
  }

  .profile-notes {
    @media (min-width: 576px) {
      padding-left: 6rem;
    }

    .profile-header {
      font-family: "Bitter", serif;
      font-size: 5rem;
      color: white;
      margin: 0;
      margin-bottom: 1rem;

      @media (max-width: 575.98px) {
        font-size: 3rem;
        margin: 2rem 0 1rem;
      }
    }

    p {
      font-family: "Lato", sans-serif;
      font-size: 1.4rem;
      line-height: 1.6;
      color: white;

      .imp {
        position: relative;
        z-index: 1;
        font-weight: 600;
        &:before {
          content: "";
          width: 100%;
          height: 13px;
          background: #6101fe;
          position: absolute;
          bottom: 2px;
          left: 0;
          z-index: -1;
        }
      }

      .hero-color {
        color: $green;
      }

      .brand {
        font-weight: 600;
      }
    }
  }
}

.section-oss {
  background: $lightGray;
  .section-title-container {
    background: $lightGray;
    border-top: 10px solid $purple;
  }
  .section-title {
    color: $white;
  }

  .project-container {
    @media (max-width: 575.98px) {
      margin: 1rem 0;
    }
  }

  .project-name {
    color: $white;
    border-bottom: 2px solid $white;
    font-family: "Lato", sans-serif;
    padding-bottom: 1.2rem;
    font-size: 1.2rem;
    font-weight: 700;
  }

  .project-desc {
    color: #eaeaea;
    font-family: "Lato", sans-serif;
    font-size: 1rem;
    margin-top: 0;
    line-height: 1.4;
  }

  .project-link {
    text-decoration: none;
    font-family: "Lato", sans-serif;
    font-size: 1rem;
    color: #00f7cf;
  }

  .profile-link {
    font-family: "Lato", sans-serif;
    font-size: 1.4rem;
    color: $white;
    margin-top: 5rem;
    margin-bottom: 0;
    font-weight: 700;
    background: none;
    border: 1px solid white;
    padding: 1rem 2rem;
    box-shadow: 3px 3px 0px;
    cursor: pointer;

    @media (max-width: 575.98px) {
      margin-top: 3rem;
    }

    &:hover {
      box-shadow: 5px 5px 0px;
      position: relative;
      right: 2px;
      bottom: 2px;
    }

    &:active {
      box-shadow: 1px 1px 0px;
      position: relative;
      right: 0px;
      bottom: 0px;
    }

    button {
      background: none;
      outline: none;
      color: white;
    }
  }
}

.section-video {
  background: $white;
  border-top: 10px solid $lightGray;
  .section-title-container {
    background: $white;
    border-top: 10px solid $green;
  }
  .section-title {
    color: $lightGray;
  }
  .video-name {
    color: $white;
    background: $purple;
    font-family: "Lato", sans-serif;
    font-size: 1rem;
    font-weight: 700;
    padding: 0 0 0.5rem;
    position: relative;
    top: -0.5rem;
    right: -0.6rem;
  }
  .iframe-container {
    box-shadow: 10px 10px 0 $purple;
  }

  .videos {
    margin-top: 5rem;

    @media (max-width: 575.98px) {
      button {
        margin-bottom: 1rem;
      }
    }
  }
  .videos-link {
    font-family: "Lato", sans-serif;
    font-size: 1.4rem;
    color: $white;
    font-weight: 700;
    background: none;
    border: 1px solid $purple;
    color: $purple;
    padding: 1rem 2rem;
    margin: 0 1rem;
    box-shadow: 3px 3px 0px;
    cursor: pointer;

    &:hover {
      box-shadow: 5px 5px 0px;
      position: relative;
      right: 2px;
      bottom: 2px;
    }

    &:active {
      box-shadow: 1px 1px 0px;
      position: relative;
      right: 0px;
      bottom: 0px;
    }
    
    button {
      background: none;
      outline: none;
      color: $purple;
    }
  }
}

.section-writing {
  background: $purple;
  border-top: 10px solid white;
  .section-title-container {
    background: $purple;
    border-top: 10px solid $green;
  }
  .section-title {
    color: $white;
  }
  .articles-list {
    list-style-type: none;
    margin: 0;
    padding: 0;
  }
  .article {
    margin: 2rem 0;
  }
  .article-header-link {
    text-decoration: none;
    &:hover {
      text-decoration: underline;
      text-decoration-color: $lightGray;
    }
  }
  .articles-container {
    background: #fff;
    box-shadow: -10px 10px 0 $lightGray;
    width: 700px;
    padding: 2rem;

    @media (max-width: 575.98px) {
      width: 100%;
    }
  }
  .article-header {
    color: $lightGray;
    font-family: "Bitter", serif;
    font-weight: 700;
    font-size: 2rem;
    margin: 0;
  }
  .article-desc {
    font-family: "Lato", sans-serif;
    color: $lightGray;
    font-size: 1.2rem;
    margin: 0.6rem 0 0.8rem;
  }
  .article-link {
    font-family: "Bitter", serif;
    font-size: 1.2rem;
    color: #00f7cf;
    background-color: $lightGray;
    padding: 0.2rem 0.4rem;
    text-decoration: none;
    &:hover {
      text-decoration: underline;
      text-decoration-color: #00f7cf;
    }
  }
  .featured-container {
    max-width: 400px;
    background: white;
    box-shadow: -10px 10px 0 $lightGray;

    @media (max-width: 575.98px) {
      max-width: 100%;
    }

    img {
      max-width: 400px;

      @media (max-width: 575.98px) {
        max-width: 100%;
      }
    }
    a {
      text-decoration: none;
      &:hover {
        text-decoration: underline;
        text-decoration-color: $lightGray;
      }
    }
    .featured-content {
      padding: 1rem;
    }
    .featured-header {
      color: $lightGray;
      font-family: "Bitter", serif;
      font-weight: 700;
      font-size: 2rem;
      margin: 0;
    }
    .featured-desc {
      font-family: "Lato", sans-serif;
      color: $lightGray;
      font-size: 1.2rem;
      margin: 0.6rem 0 0.8rem;
    }
  }
}

.section-talks {
  border-top: 10px solid $green;
  background: $lightGray;
  .section-title-container {
    background: $lightGray;
    border-top: 10px solid $purple;
  }
  .section-title {
    color: $white;
  }
  .talk-name {
    font-family: "Lato", sans-serif;
    font-size: 1rem;
    font-weight: 700;
    position: relative;
    padding: 0 0 0.5rem;
    top: -0.5rem;
    right: -0.6rem;
    background: #111;
    color: $white;
  }
  .iframe-container {
    padding-top: 61.75%;
    box-shadow: 10px 10px 0 #111;
  }
}

.footer {
  background: $purple;
  border-top: 10px solid $green;
  padding: 2rem 1rem;

  .footer-social {
    padding: 2rem 0;
  }

  .footer-text {
    font-family: "Lato", sans-serif;
    font-size: 1rem;
    font-weight: 700;
    color: white;
  }

  .social-img {
    margin: 0 0.5rem;
  }
}