.flex {
  display: flex;
}

.flex-row {
  flex-direction: row;
}

.flex-column {
  flex-direction: column;
}

.flex-mobile-column {
  @media (max-width: 575.98px) {
    flex-direction: column;
  }
}

.flex-grow-1 {
  flex-grow: 1;
  flex-basis: 0;
}

.jc-center {
  justify-content: center;
}

.jc-end {
  justify-content: flex-end;
}

.jc-space-between {
  justify-content: space-between;
}

.ai-center {
  align-items: center;
}

.ai-center-mobile {
  @media (max-width: 575.98px) {
    align-items: center;
  }
}
