.newsletter-form {
  background: white;
  box-shadow: -10px 10px 0 #202020;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 800px;

  @media (max-width: 575.98px) {
    width: 100%;
  }
}

.newsletter-header {
  font-family: "Bitter", serif;
  font-size: 3rem;
  margin: 0;
  color: #202020;
  text-align: center;

  @media (max-width: 575.98px) {
    font-size: 2rem;
  }
}

.newsletter-text {
  font-family: "Lato", sans-serif;
  font-size: 1.2rem;
  text-align: center;
}

.newsletter-input {
    width: 50%;
    font-family: "Lato", sans-serif;
    font-size: 1.2rem;
    border: 1px solid #202020;
    padding: 0.3rem 0.5rem;

    @media (max-width: 575.98px) {
      width: 100%;
    }
}

.newsletter-button {
  font-family: "Lato", sans-serif;
  font-size: 1.4rem;
  color: #6200fe;
  margin-top: 3rem;
  margin-bottom: 0;
  font-weight: 700;
  background: none;
  border: 1px solid #6200fe;
  padding: 1rem 2rem;
  box-shadow: 3px 3px 0px;
  cursor: pointer;

  &:hover {
    box-shadow: 5px 5px 0px;
    position: relative;
    right: 2px;
    bottom: 2px;
  }

  &:active {
    box-shadow: 1px 1px 0px;
    position: relative;
    right: 0px;
    bottom: 0px;
  }

  button {
    background: none;
    outline: none;
    color: #6200fe;
  }
}
